<template>
  <div>
    <form class="pure-form pure-form-aligned"
          v-if="queryType !== 'all'">
      <fieldset>
        <legend v-if="queryType === 'user'">查询指定账户信息</legend>
        <legend v-else>查询指定账户历史记录</legend>

        <div class="pure-control-group">
          <label for="username">用户名</label>
          <input type="text" id="username" name="username" disabled
                 :value="username">
        </div>
        <div class="pure-control-group">
          <label for="operatename">账户名</label>
          <input type="text" id="operatename" name="operatename" placeholder="账户名"
                 v-model="operatename">
        </div>

        <div class="pure-controls">
          <input type="submit" value="提交" class="pure-button pure-button-primary"
                 @click.prevent="query">
        </div>
      </fieldset>
    </form>

    <p v-if="queryType === 'all'">所有账户信息</p>
    {{queryResult}}
  </div>
</template>

<script>
  import createdWS from '../mixins/createdWS'
  import createdUsername from '../mixins/createdUsername'

  export default {
    name: "Query",
    mixins: [createdUsername, createdWS],

    props: {
      queryType: String
    },

    data: function () {
      return {
        username: '',
        requesttype: '',
        operatename: '',
        queryResult: '',
        ws: ''
      }
    },

    created() {
      const self = this
      this.ws.addEventListener('message', event => {
        const res = JSON.parse(event.data)

        if (!res.ifsuccessful) return alert(res.errinfo)
        self.queryResult = res.result
      })

      if (this.queryType === 'all') {
        this.requesttype = 'queryAll'
        setTimeout(() => {
          self.query()
        }, 500)
      } else if (this.queryType === 'user') {
        this.requesttype = 'queryUser'
      } else {
        this.requesttype = 'getHistory'
      }
    },

    methods: {
      query() {
        const data = JSON.stringify({
          username: this.username,
          requesttype: this.requesttype,
          operatename: this.operatename
        })
        this.ws.send(data)
      }
    }
  }
</script>

<style scoped>

</style>



