/**
 * 待混入的组件需要在data里声明ws
 */

export const mixin = {
  created() {
    const ADDRESS = 'ws://localhost:8080/ws'
    this.ws = new WebSocket(ADDRESS)
    console.log('create')
  },

  beforeDestroy() {
    this.ws.close()
  }
}

export default mixin



