<template>
  <div>
    <form class="pure-form">
      <fieldset>
        <legend>注册上链</legend>
        <input type="text" id="username" name="username" placeholder="用户名"
               v-model="username">
        <input type="submit" value="提交" class="pure-button pure-button-primary"
               @click.prevent="enroll">
      </fieldset>
    </form>
  </div>
</template>

<script>
  import createdWS from '../mixins/createdWS'

  export default {
    name: "Enroll",
    mixins: [createdWS],

    data: function() {
      return {
        username: '',
        requesttype: 'initUser',
        ws: ''
      }
    },

    created() {
      this.ws.addEventListener('message', event => {
        const res = JSON.parse(event.data)

        if (!res.ifsuccessful) return alert(res.errinfo)
        localStorage.setItem('username', this.username)
        alert('注册成功！')
      })
    },

    methods: {
      enroll() {
        const data = JSON.stringify({
          username: this.username,
          requesttype: this.requesttype
        })
        this.ws.send(data)
      }
    }
  }
</script>

<style scoped>

</style>



